$('.idNumber').on('focus', function () {
    $('.idItem').removeClass('error');
});
function validate() {
    var idNumber = $('.idNumber').val() && $('.idNumber').val().trim();
    var mobile = $('.idNumber').val() && $('.mobile').val().trim();
    if (!idNumber) {
        $('.idItem').addClass('error');
        return;
    }
    return {
        idNumber: idNumber,
        mobile: mobile
    };
}
